function sliderLayouts() {
    var swiper = new Swiper('.layouts .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 30,
        navigation: {
            nextEl: ".layouts .swiper-button-next",
            prevEl: ".layouts .swiper-button-prev",
        },
    })
}

function sliderWorks() {
    var swiper = new Swiper('.works .mySwiper', {
        slidesPerView: 3,
        spaceBetween: 15,
        navigation: {
            nextEl: ".works .swiper-button-next",
            prevEl: ".works .swiper-button-prev",
        },
        pagination: {
            el: ".works .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1.3,
                spaceBetween: 10,
            },
            451: {
                slidesPerView: 1.5,
                spaceBetween: 10,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 10,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 15,
            },
            993: {
                slidesPerView: 2.5,
                spaceBetween: 15,
            },
            1401: {
                slidesPerView: 3,
                spaceBetween: 15,
            }
        }
    })
}

$(document).ready(function () {
    sliderLayouts();
    sliderWorks();

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-mob').toggleClass('header-mob--active')
        $('body').toggleClass('fixed-body')
    })

    $(".header__list li a").click(function() {
        $(".burger").removeClass("burger--active");
        $(".header-mob").removeClass("header-mob--active");
        $("body").removeClass("fixed-body");
    });

    //Input
    $('input').on('input', function() {
        var $placeholder = $(this).siblings('.placeholder');
        if ($(this).val() === '') {
            $placeholder.show();
        } else {
            $placeholder.hide();
        }
    });
})